<template>
  <div class="va-chat">
    <div
      class="va-chat__body"
      :style="{ backgroundColor: '#00000017' }"
      v-sticky-scroll="{
        animate: true,
        duration: 200,
      }"
    >
      <div class="va-chat__body-messages">
        <div
          class="va-chat__message"
          v-for="(message, index) in value"
          :key="index"
          :style="{
            ...(message.yours ? computedYoursStyle : computedStyle),
            paddingTop: !message.delivered ? '1.4rem' : '0.657rem',
          }"
          :class="{ 'va-chat__message--yours': message.yours }"
        >
          <div
            class="va-chat__message-info"
            :style="{
              left: message.yours ? 'auto' : '0.5rem',
              right: message.yours ? '0.5rem' : 'auto',
            }"
          >
            <va-icon
              name="fa fa-spinner fa-spin"
              v-if="!message.delivered"
            ></va-icon>
          </div>

          <span
            class="va-chat__message-text"
            :style="{
              textAlign: message.yours ? 'right' : 'left',
            }"
          >
            {{ message.text }}
          </span>
          <span
            class="va-chat__message_date"
            :style="{
              opacity: 0.4,
              textAlign: message.yours ? 'right' : 'left',
            }"
          >
            {{ message.date }}
          </span>
        </div>
      </div>
    </div>
    <div class="va-chat__controls">
      <va-input
        :autosize="true"
        v-model="inputMessage"
        @keypress.ctrl.enter="sendMessage"
        :placeholder="$t('dashboard.support.placeholder')"
        class="va-chat__input"
        type="textarea"
      />
      <va-button @click="sendMessage">
        {{ $t('buttons.send') }}
      </va-button>
    </div>
  </div>
</template>

<script>
import { StickyScroll } from '../../../services/vuestic-ui/components'

export default {
  name: 'chat',
  directives: { StickyScroll },
  data() {
    return {
      inputMessage: '',
    }
  },
  computed: {
    computedStyle() {
      return { backgroundColor: this.$themes.darkForeground, color: 'white' }
    },
    computedYoursStyle() {
      return { backgroundColor: this.$themes.info, color: 'white' }
    },
  },
  props: {
    value: Array,
  },
  methods: {
    scrollToBottom() {
      setTimeout(() => {
        var messages = document.querySelector('.va-chat__body')
        messages.scrollTop = messages.scrollHeight - messages.clientHeight
      }, 10)
    },
    sendMessage() {
      this.inputMessage = this.inputMessage.replace(/^[\n\s]+|[\n\s]+$/g, '')

      if (!this.inputMessage) {
        return
      }

      this.$emit('input', {
        text: this.inputMessage,
        delivered: false,
        yours: true,
      })

      setTimeout(() => {
        this.$el.querySelector('.va-chat__input').focus()
        this.inputMessage = ''
      }, 10)
    },
  },
  mounted() {
    this.$root.$on('chat-update', this.scrollToBottom)
    this.scrollToBottom()
  },
  destroy() {
    this.$root.$off('chat-update', this.scrollToBottom)
  },
}
</script>

<style lang="scss">
.chat {
  &__content {
    @include va-flex-center();
  }
}

$chat-message-br: 0.65rem;

.va-chat {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-rows: 1fr auto;

  &__body {
    padding: 20px 10px;
    width: 100%;
    height: 100%;
    min-height: 26rem;
    overflow-y: auto;

    &-messages {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      max-height: 300px;
    }
  }

  &__message {
    position: relative;
    padding: 0.657rem 1.375rem;
    margin-bottom: $chat-message-br;
    max-width: 70%;
    overflow-wrap: break-word;
    border-radius: 0.5rem;
    border-top-left-radius: 0;
    align-self: flex-start;
    background-color: $light-gray2;
    transition: padding 0.3s;

    &-info {
      position: absolute;
      top: 0.4rem;
      font-size: 0.6rem;
    }

    &-text {
      display: block;
      transform: translateY(-2px);
    }

    &:last-child {
      margin-bottom: 0;
    }

    &--yours {
      align-self: flex-end;
      border-top-right-radius: 0;
      border-top-left-radius: 0.5rem;
    }

    &_date {
      font-family: sans-serif;
      font-size: 0.8rem;
      opacity: 0;
      transition: opacity 0.5s;
    }
  }

  &__controls {
    display: flex;
    align-items: flex-start;
    height: 100%;
    width: 100%;
    margin: 0 0;
    margin-top: 1rem;
  }

  &__input {
    color: white;
  }
}
</style>
